* {
  margin: 0;
  padding: 0;
}

html, 
body {
  font-size: 18px;
  background-color: #f5f6f7;
}

h1 {
  max-width: 940px;
  font-size: 2.5em;
  font-family: Lato, sans-serif;
  font-weight: 700;
  line-height: 1.1;
  margin: 40px 0;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.contenedor-principal {
  min-height: 600px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}


@media only screen and (max-width: 1300px) {
  .contenedor-principal {
    min-height: 600px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 0 100px;
  }
}

#light {
  background-color: #f5f6f7;
}

#dark {
  background-color: #1b1b32;
  color: #f5f6f7;
}

#dark .contenedor-testimonio {
  background-color: #0a0a23;
}

.switch {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  bottom: 20px;
  right: 20px;
  filter: drop-shadow(1px 2px 5px rgba(10, 10, 35, 0.708));
}
label {
  margin-bottom: 5px;
  font-weight: 500;
}

